.homepage {
  margin: 10vw 0;
  height: fit-content;
  margin-bottom: -75vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  overflow: hidden;

}

.sections {
  width: 80vw;
  margin: 0vw 0;
  position: relative;
  overflow: hidden;
}

.section-1 {
  margin-left: -10vw;
  display: flex;
  align-items: left;
}

.im1,
.im2 {
  transform: translateY(30vh);
  object-fit: cover;
  backface-visibility: hidden;
}

.im3 {
  transform: translateY(40vh);
  object-fit: cover;
  backface-visibility: hidden;
}

/* Styles for the first section */
.section-1 .background-image,
.section-1 .background-video {
  z-index: 2;
  width: 30vw;
  height: 40vw;
  position: relative;
}

.section-2 {
  transform: translateY(-12vw);
  position: absolute;
  margin-left: 55vw;
  width: 35vw;
}

/* Styles for the second section */
.section-2 .background-image,
.section-2 .background-video {
  z-index: 2;
  width: 35vw;
  height: 20vw;
  position: relative;
}

.txt-con {
  flex: 1;
  transform: translateX(2vw);
  margin: 0 0;
  font-family: 'CustomFont2', sans-serif;
  text-align: left;
  font-size: 4vw;
  line-height: 1;
  padding: 0 0;
  letter-spacing: -0.15rem;
  font-weight: 100;
}

.txt-con-two {
  flex: 1;
  transform: translateY(-3vw);
  margin: 0 0;
  font-family: 'CustomFont2', sans-serif;
  text-align: left;
  font-size: 4vw;
  line-height: 1;
  padding: 0 0;
  letter-spacing: -0.15rem;
  font-weight: 100;
}

.txt-con .two,
.txt-con-two .two {
  margin-top: -2vw;
  font-size: 0.85vw;
  letter-spacing: normal;
}

.meow {
  display: flex;
  transform: translate(-20vw, -1vw);
  font-size: 2rem;
  width: 38vw;
  margin: 5vw 0;
  text-align: left;
}

.meow a {
  font-size: 1.3rem;
  text-decoration: none;
  color: black;
}

.meow a::after {
  content: "→";
  display: inline-block;
  margin-left: 1vw;
  transition: transform 200ms ease-in-out;
}

.meow a:hover::after {
  transform: translateX(0.4rem);
}

.meow a:hover {
  color: #0CEE72;
}

.section-3 .background-image,
.section-3 .background-video {
  z-index: 2;
  width: 15vw;
  height: 20vw;
  position: relative;
}

.section-3 {
  width: 30vw;
  display: flex;
  align-items: left;
  transform: translateY(-7vw);
}

.txt-con-three {
  font-family: 'CustomFont2', sans-serif;
  text-align: left;
  font-size: 4vw;
  line-height: 1;
  padding: 0 0;
  letter-spacing: -0.15rem;
  font-weight: 100;
  transform: translateX(2vw);

}

.txt-con-three .two {
  margin-top: -2vw;
  font-size: 0.85vw;
  letter-spacing: normal;
}

@media (max-width:1200px) {

  .im1,
  .im2 {
    transform: translateY(28vh);
  }

  .im3 {
    transform: translateY(35vh);
  }
}

@media(max-width:980px) {
  .meow {
    margin: 5vw 0;
    width: 49vw;
    font-size: 2.5vw;
  }

  .im1,
  .im2 {
    transform: translateY(33vh);
  }

  .im3 {
    transform: translateY(43vh);
  }

  .homepage {
    margin-bottom: -80vh;
  }

  .meow a {
    font-size: 2vw;
  }

  .txt-con .two,
  .txt-con-two .two,
  .txt-con-three .two {
    font-size: 1.5vw;
  }

  .txt-con-three {
    transform: translateX(3vw);
  }

  .section-3 .background-image,
  .section-3 .background-video {
    width: 20vw;
    height: 25vw;
    position: relative;
  }

  .section-3 {
    width: 40vw;
  }
}

@media (max-width:800px) {
  .homepage {
    margin-bottom: 0;
  }

  .im1,
  .im2,
  .im3 {
    transform: translateY(0);
  }
}

@media(max-width: 700px) {

  .txt-con .two,
  .txt-con-two .two,
  .txt-con-three .two {
    display: none;
  }

  .homepage .meow {
    order: 4;
  }



  .section-1 {
    width: 100vw;
    height: 100vh;
    order: 1;
    margin-left: -40vw;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .section-1 .background-image,
  .section-1 .background-video {
    width: 46vw;
    height: 57vw;
  }

  .txt-con {
    width: 80%;
    font-size: 5vw;
    text-align: right;
    transform: translateY(-30vh);
  }

  .section-2 {
    transform: translate(0vw, -35vw);
    order: 2;
    width: 80%;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  .section-2 .background-image,
  .section-2 .background-video {
    width: 60vw;
    height: 35vw;
  }

  .txt-con-two {
    width: 100%;
    font-size: 5vw;
    transform: translate(-7vw, -12vw);
    text-align: left;
  }

  .section-3 {
    width: 100%;
    display: flex;
    order: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    transform: translate(-20vw, -9vw);
  }

  .section-3 .background-image,
  .section-3 .background-video {
    width: 45vw;
    height: 55vw;
  }

  .txt-con-three {
    text-align: right;
    width: 100%;
    font-size: 5vw;
    transform: translate(12vw, -17vw);
  }

  .meow {
    width: 90%;
    transform: translate(0, -15vw);
    margin: 0 0;

    font-size: 4vw;
  }

  .meow a {
    font-size: 3vw;
  }
}

@media(max-width:500px) {
  .txt-con {
    transform: translateY(-40vh);
  }

  .txt-con-two {
    transform: translate(1vw, -8vh);
  }

  .txt-con-three {
    transform: translate(-10vw, -11vh);
  }
}